.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9999;
}
.header__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @media screen and (min-width: $tablet) {
    padding-left: 32px;
    padding-right: 32px;
    flex-direction: row;
    justify-content: space-between;
  }
  @media screen and (min-width: $desktop) {
    max-width: 1360px;
  }
}
